/* FontFace
/* ---------------------------------------------------------- */

@font-face {
  font-family: 'Poppins';
  src: url('./fonts/Poppins-Regular.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Poppins';
  src: url('./fonts/Poppins-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: 'Upheaval';
  src: url('./fonts/upheavtt.ttf') format('truetype');
  font-weight: 400;
  font-style: normal;
}

:root {
  --poppins: -apple-system, BlinkMacSystemFont, "Poppins", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  --upheaval: -apple-system, BlinkMacSystemFont, "Upheaval", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

/* ---------------------------------------------------------- */

.introductionSection {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: #C8DCFF;
}

.introductionWrapper {
  margin-top: 85px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-height: 870px) {
  .introductionWrapper {
    margin-top: 200px !important;
  }
}

.introductionSection h1 {
  font-family: var(--upheaval);
  font-size: 90px;
  color: #fff;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #000;
  text-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
}

@media screen and (max-width: 900px) {
  .introductionSection h1 {
    font-size: 70px;
  }
}



.introductionSection p {
  margin-top: 10px;
  font-family: var(--upheaval);
  font-size: 24px;
  color: #242424;
  max-width: 411px;
  text-align: center;
}



.introductionSection h1 span {
  color: #1A1A1A;
  -webkit-text-stroke-color: #9D2F8C;
}

.socialMediaWrapper {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
  width: 100px;
  margin-bottom: 60px;
}

.socialMediaWrapper img {
  transition: 200ms;
}

.socialMediaWrapper img:hover {
  transform: scale(1.1);
}

.floorIntroductionImage {
  /* justify-self: flex-end; */
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.stoneFloor {
  width: 100vw;
}

.allBonecos {
  position: absolute;
  bottom: 18px !important;
  left: calc(50vw - 540px);
}

.purpleButton {
  padding: 12px 46px;
  background-color: #450E3C;
  color: #FFF;
  font-family: var(--upheaval);
  font-size: 24px;
  border-radius: 5px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  text-decoration: none;
}

.purpleButton:hover {
  background-color: #57124b;
}



.infoHumanithereum {
  border-top: 6px solid #2E2E2E;
  background-color: #1A1A1A;
  padding: 90px 100px 80px;
  display: flex;
  justify-content: center;
}

.infoHumanithereumWrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1166px;
}

.infoHumanithereumText {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 450px;
  margin-right: 238px;
}

.infoHumanithereumText h2 {
  font-family: var(--upheaval);
  color: #F1F1F1;
  font-size: 36px;
  margin-bottom: 50px;
}

.infoHumanithereumText p {
  font-family: var(--poppins);
  color: #D3D3D3;
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 24px;
}

.infoHumanithereumImages {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
}

.roadmapHumanithereum {
  background-color: #C8DCFF;
  font-family: var(--upheaval);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 70px 80px;
  color: 19px;
}

.roadmapHumanithereum h2 {
  font-size: 36px;
}

.roadmapHumanithereum p {
  margin-top: 20px;
  width: 150px;
  font-size: 18px;
  text-align: center;
}

.roadmapFrame {
  margin-top: 90px;
  display: flex;
  justify-content: space-between;
  width: 1205px;
  margin-bottom: 70px;
}

.roadmapItem {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.roadmapItem img {
  transition: 400ms;
}

.roadmapItem img:hover {
  transform: scale(1.1);
}

.blackButton {
  padding: 12px 46px;
  color: #fff;
  font-size: 18px;
  background-color: #222222;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
}

.blackButton:hover {
  background-color: #111;
}

.ideasHumanithereum {
  padding: 80px 100px;
  background-color: #1A1A1A;
  color: #FFF;
  font-family: var(--upheaval);
  display: flex;
  justify-content: center;
}

.ideasHumanithereumWrapper {
  max-width: 1166px;
  display: flex;
  flex-direction: column;
}

.ideasHumanithereum h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.ideasHumanithereum p {
  font-family: var(--poppins);
  font-size: 16px;
  color: #D3D3D3;
}

.ideasFrame {
  width: 1166px;
  display: flex;
  justify-content: space-between;
  padding: 0 45px;
}

.ideasItem {
  margin-top: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 75px;
}



.ideasItem p {
  font-family: var(--upheaval);
  color: #fff;
  text-align: center;
  margin-bottom: 20px;
}

.ideasHumanithereum .purpleButton {
  align-self: center;
}

.teamHumanithereum {
  padding: 70px 100px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.teamHumanithereum h2 {
  font-family: var(--upheaval);
  font-size: 36px;
  color: #191919;
  margin-bottom: 70px;
}

.teamHumanithereumFrame {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.teamHumanithereumFrame img {
  width: 112px;
}

.teamHumanithereumFrame p {
  font-family: var(--poppins);
  font-size: 14px;
  text-align: center;
  width: 323px;
  line-height: 21px;
}

.teamHumanithereumFrame p:nth-child(2) {
  width: unset;
  margin-top: 10px;
  font-size: 18px;
  font-family: var(--upheaval);
  line-height: unset;
  margin-bottom: 20px;
}

.medium {
  font-weight: 500;
}

footer {
  background-color: #191919;
  color: #D3D3D3;
  font-family: var(--poppins);
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 28px 50px;
}

.socialMediaFooter {
  position: absolute;
  width: 70px;
  display: flex;
  justify-content: space-between;
  right: 50px;
}

.socialMediaFooter img {
  transition: 200ms;
}

.socialMediaFooter img:hover {
  transform: scale(1.1);
}

@media screen and (max-width: 640px) {
  .introductionSection {
    height: 70vh;
  }
  .introductionSection h1 {
    font-size: 40px;
    -webkit-text-stroke-width: 1px;
  }
  .introductionSection p {
    font-size: 18px;
    max-width: 305px;
  }
  .purpleButton {
    font-size: 18px;
    margin: 0 auto;
    /* width: 100%;
    text-align: center; */
  }
  .allBonecos {
    width: 300px;
    bottom: 52px;
    left: calc(50vw - 150px);
  }

  .infoHumanithereum {
    padding: 50px 30px;
    flex-direction: column;
  }

  .infoHumanithereumWrapper {
    flex-direction: column;
  }

  .infoHumanithereumText {
    max-width: unset;
    margin-right: unset;
    margin-bottom: 70px;
  }

  .infoHumanithereumText h2 {
    font-size: 26px;
    line-height: 22px
  }

  .infoHumanithereumText p:nth-child(4) {
    margin-bottom: 70px;
  }

  .infoHumanithereumImages {
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .roadmapFrame {
    flex-direction: column;
  }

  .roadmapItem {
    margin-bottom: 40px;
  }

  .blackButton {
    font-size: 18px;
  }

  .ideasHumanithereum {
    padding: 60px 30px;
    flex-direction: column;
  }

  .ideasHumanithereum h2 {
    font-size: 26px;
    line-height: 22px
  }

  .ideasHumanithereum p {
    font-size: 16px;
    line-height: 21px;
  }

  .ideasFrame {
    flex-direction: column;
    width: unset;
  }



  footer {
    padding: 28px 30px;
    font-size: 12px;
    justify-content: flex-start;
  }

  .socialMediaFooter {
    right: 30px;
  }
}